.hero {
    padding-top: 105px;
    padding-bottom: 100px;
    height: max-content;

    &__content-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__content {
        max-width: 560px;
    }

    &__sub-title-wrap {
        color: #141313;
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: -0.96px;
        margin-bottom: 48px;

        h4 {
            strong {
                font-style: italic;
            }
        }
    }

    h1 {
        margin-bottom: 24px;
    }

    &__contact-wrap {
        display: flex;
        flex-wrap: wrap;

        a {
            margin-right: 48px;

            &:nth-child(2n + 2) {
                margin-right: 0;
            }
        }
    }

    &__phone {

    }

    &__email {

    }

    &__contact-item {

    }

    &__contact-icon {

    }

    &__btn {
        margin-bottom: 64px;
    }

    &__image {

    }

    &__text {

    }

    &__user-wrap {
        position: relative;
        z-index: 1;
        max-width: 530px;

        &:before,
        &:after {
            position: absolute;
            content: '';
            display: block;
            z-index: 1;
        }

        &:before {
            width: 658px;
            height: 713px;
            z-index: -1;
            left: -80px;
            top: 34px;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
            background-size: contain;
        }

        &:after {
            width: 619px;
            height: 719px;
            z-index: 2;
            left: -40px;
            top: 30px;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
            background-size: contain;
        }
    }

    &__user-img {
        width: 100%;
        height: auto;
        object-fit: cover;
        max-height: 690px;
    }
}
